:root {
  --container-width: 1216px;
  --container-padding: 16px;

  // Fonts
  --font-default: "Roboto", sans-serif;

  --fs-base: 16px;
  --fw-300: 300;
  --fw-400: 400;
  --fw-500: 500;
  --fw-700: 700;

  --title-lh: 1.2;
  --body-lh: 1.5;

  // Colors
  --color-white: #fff;
  --color-black: #000;

  --color-body: #333;
  --color-title: #212121;
  --color-text-gray: #828282;

  --color-primary: #00ba35;
  --color-primary_hover: #00cc3a;
  --color-primary_active: #00992b;

  // Accent colors
  --color-accent-yellow: #fbba14;
  --color-accent-purple: #8954ec;
  --color-accent-blue: #2892d9;
  --color-accent-red: #f03d22;

  --color-success: #00ba35;
  --color-error: #d20b0b;
  --color-warning: #e69d0f;

  --color-bg_gray: #f0f1f5;
  --color-bg_dark-1: #212121;
  --color-bg_dark-2: #333333;

  --color-line-gray: rgba(0, 0, 0, 0.15);
  --color-border-dark: rgba(0, 0, 0, 0.15);
}
